.heroText {
    margin: auto;
    margin-top: 20px;
    width: 80%;
    font-size: xx-large;
    color: white;
}

img {
    height: 50%;
    width: 50%;
    max-width: 500pxg;
    margin: 20px;
}

.homeContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
    margin: 20px;
    color: white;
    background-image: linear-gradient(rgba(0,0,0,60%), rgba(0,0,0,60%)), url("./images/mike-petrucci-c9FQyqIECds-unsplash.jpg");
    background-size: cover;
    height: 100%;
}

.pictureCredit {
    align-self: flex-end;
    margin-right: 10px;
    font-size: small;
    color: white;
}

a {
    color: white;
}

@media screen and (max-width: 600px) {
    .heroText {
        font-size: medium;
    }
}